* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

$colorFont: #ff512f;
$colorBg: #ffe259;
$colorGradientBg: #ffa751;
$fontFamily: courier new, courier, monospace;

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(to right, $colorGradientBg, $colorBg);

  &__title {
    font-size: 48px;
    color: $colorFont;
    font-family: $fontFamily;
  }

  &__settings-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
  }

  &__settings-block {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  &__settings-title {
    margin-right: 20px;
    font-size: 25px;
    font-weight: bold;
    color: $colorFont;
    font-family: $fontFamily;
  }

  &__input {
    border-radius: 10px;
    outline: 2px solid $colorFont;
    border: 0;
    background-color: #ffa751;
    outline-offset: 3px;
    padding: 5px;
    transition: 0.25s;
    color: $colorFont;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
    font-family: $fontFamily;

    &:focus {
      outline-offset: 5px;
      background-color: #f7872b;
    }

  }

  &__input-checkbox {
    position: relative;
    top: 0;
    width: 20px;
    height: 20px;
    -webkit-appearance: none;
    outline: none;
    transition: 0.5s;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 8px;
      border: 2px solid $colorFont;
      box-sizing: border-box;
      transition: 0.5s;
    }

    &:checked:before {
      border-left: none;
      border-top: none;
      width: 10px;
      border-radius: 1px;
      border-color: rgb(24, 196, 24);
      transform: rotate(45deg) translate(5px, -10px);
    }
  }
}
